function Detail(props) {
    return (
        <>
            <p>
                <a className="place-detail-grid" data-bs-toggle="collapse" href="#multiCollapseExample1" role="button" aria-expanded="false" aria-controls="multiCollapseExample1">
                    <div className="Place-detail">
                        <h2 className="place-category"><i class="bi bi-arrow-90deg-right"></i> Historical or Heritage attractions</h2>
                    </div>
                </a>

            </p>
            <div className="row">
                <div className="col">
                    <div className="collapse multi-collapse" id="multiCollapseExample1">
                        <div className="card card-body">
                            <div className="grid-list">
                                <Places title={places_list.title[0]} pic={places_list.pic[0]} link={places_list.link[0]} />
                                <Places title={places_list.title[1]} pic={places_list.pic[1]} link={places_list.link[1]} />
                                <Places title={places_list.title[2]} pic={places_list.pic[2]} link={places_list.link[2]} />
                                <Places title={places_list.title[3]} pic={places_list.pic[3]} link={places_list.link[3]} />
                                <Places title={places_list.title[4]} pic={places_list.pic[4]} link={places_list.link[4]} />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}
function Places(props) {
    return (
        <div className="place_div">
            <h3>{props.title}</h3>
            <div className='detail-tile'>
                <img src={props.pic} alt="" />
                <iframe src={props.link} width="500" height="345" allowFullScreen="" loading="lazy"></iframe>
            </div>
        </div>
    )
}
export default Detail