import './App.css';
import Header from './components/header';
import Body from './components/body';
import GridBody from './components_grid/body';
import Footer from './components_grid/footer';
import AboutBody from './about_components/body'
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
function App() {
  return (
    <Router>
      <div className="App">
        <Switch>
          <Route path="/home">
            <Header />
            <Body />
            <Footer />
          </Route>
          <Route path="/explore">
            <Header />
            <GridBody />
          </Route>
          <Route path='/about'>
            <Header fixed={false}/>
            <AboutBody />
            <Footer />
          </Route>
        </Switch>
      </div>
    </Router>
  );
}

export default App;
