import react from 'react';
import './grid.css';
import Detail from './historic';
import Nature from './natural';
import ManMade from './manmade';
import Footer from './footer';
function GridBody() {
    var TxtType = function (el, toRotate, period) {
        this.toRotate = toRotate;
        this.el = el;
        this.loopNum = 0;
        this.period = parseInt(period, 10) || 2000;
        this.txt = '';
        this.tick();
        this.isDeleting = false;
    };

    TxtType.prototype.tick = function () {
        var i = this.loopNum % this.toRotate.length;
        var fullTxt = this.toRotate[i];

        if (this.isDeleting) {
            this.txt = fullTxt.substring(0, this.txt.length - 1);
        } else {
            this.txt = fullTxt.substring(0, this.txt.length + 1);
        }

        this.el.innerHTML = '<span className="wrap">' + this.txt + '</span>';

        var that = this;
        var delta = 200 - Math.random() * 100;

        if (this.isDeleting) { delta /= 2; }

        if (!this.isDeleting && this.txt === fullTxt) {
            delta = this.period;
            this.isDeleting = true;
        } else if (this.isDeleting && this.txt === '') {
            this.isDeleting = false;
            this.loopNum++;
            delta = 500;
        }

        setTimeout(function () {
            that.tick();
        }, delta);
    };

    window.onload = function () {
        var elements = document.getElementsByClassName('typewrite');
        for (var i = 0; i < elements.length; i++) {
            var toRotate = elements[i].getAttribute('data-type');
            var period = elements[i].getAttribute('data-period');
            if (toRotate) {
                new TxtType(elements[i], JSON.parse(toRotate), period);
            }
        }
        // INJECT CSS
        var css = document.createElement("style");
        css.type = "text/css";
        css.innerHTML = ".typewrite > .wrap { border-right: 0.08em solid #fff}";
        document.body.appendChild(css);
    };
    return (
        <div className='places-grid'>
            <div className='places-intro'>
                <img src="./images/places.svg" alt="" />
                <h1 href="" className="typewrite" data-period="2000" data-type='[ "Top Tourist Attractions !","Have a look !"]'>
                    <span className="wrap"></span></h1>
            </div>
            <h1 id='grid-head'>Tourist places Categories</h1>
            <ul>
                <li className="list">
                    <Detail/>
                </li>
                <li className="list">
                    <Nature/>
                </li>
                <li className="list">
                    <ManMade/>
                </li>
                
            </ul>
            <Footer/>
        </div>
    )
}
export default GridBody